<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        placeholder="用户名"
        v-model="listQuery.username"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-input
        placeholder="真实姓名"
        v-model="listQuery.name"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="searchData">搜索</el-button>
      <el-button
        class="filter-item"
        style="margin-left: 10px;"
        type="primary"
        icon="el-icon-edit"
        @click="addData()"
      >添加</el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="data"
      border
      fit
      highlight-current-row
      style="width: 100%;"
    >
      <el-table-column label="用户名" align="center" prop="username"/>
      <el-table-column label="密码" align="center" prop="password"/>
      <el-table-column label="真实姓名" align="center" prop="name"/>
      <el-table-column label="部门" align="center" prop="departmentName"/>
      <el-table-column label="职位" align="center" prop="office"/>
      <el-table-column label="是否禁用" align="center">
        <template slot-scope="scope">{{scope.row.disabled?'禁用状态':'启用状态'}}</template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="300" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="addData(scope.row)">编辑</el-button>
          <el-button
            size="mini"
            :type="scope.row.disabled?'success':'warning'"
            @click="disabledUser(scope.row.userId,scope.row.disabled)"
          >{{!scope.row.disabled?"禁用":'启用'}}</el-button>
          <el-button size="mini" @click="deleteData(scope.row.userId)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getData"
    />

    <el-dialog title="编辑" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="editData"
        label-position="left"
        label-width="70px"
        style="width: 400px; margin-left:50px;"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="editData.username"/>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="editData.password"/>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="editData.name"/>
        </el-form-item>
        <el-form-item label="职位" prop="office">
          <el-input v-model="editData.office"/>
        </el-form-item>
        <el-form-item label="部门" prop="departmentId">
          <el-cascader
            style="width:330px"
            expand-trigger="hover"
            :options="departmentList"
            v-model="editData.departmentId"
            :props="departmentProps"
            filterable
            change-on-select
          ></el-cascader>
        </el-form-item>
        <el-form-item label="角色" prop="roles">
          <el-select v-model="editData.roles" multiple placeholder="请选择角色" style="width:330px">
            <el-option label="管理员" value="admin"></el-option>
            <el-option label="部门主管" value="manager"></el-option>
            <el-option label="普通用户" value="normal"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitData('dataForm')">确定</el-button>
        <el-button type="primary" @click="cancelEdit('dataForm')">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import gql from "graphql-tag";
import { Permission } from "@/api/permission";
import Pagination from "@/components/Pagination"; // Secondary package based on el-pagination

export default {
  components: { Pagination },
  apollo: {
    getUser: {
      query: gql`
        query 
          userList($name:String,$username:String,$page:Int!, $limit: Int!){
             getUser(name:$name,username:$username,page:$page, limit: $limit) {
               name
               userId
            }
          }
         `,
      variables() {
        return{
          name: this.listQuery.name,
          username:this.listQuery.username,
          page: this.listQuery.page,
          limit: this.listQuery.limit
        }
      },
    }
  },
  data() {
    return {
      getUser: null,
      dialogFormVisible: false,
      rules: {
        username: [
          { required: true, message: "请填写用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请填写密码", trigger: "blur" }],
        roles: [{ required: true, message: "请选择角色", trigger: "blur" }],
        name: [{ required: true, message: "请填写真实姓名", trigger: "blur" }],
        departmentId: [
          { required: false, message: "请选择部门", trigger: "click" }
        ],
        office: [{ required: true, message: "请输入职位", trigger: "blur" }]
      },
      listQuery: {
        name: null,
        username: null,
        page: 1,
        limit: 10
      },
      listLoading: false,
      total: 0,
      // 需要新增和编辑的数据对象
      editData: {
        roles: [],
        userId: "",
        username: "",
        password: "",
        name: "",
        departmentId: [],
        departmentName: "",
        office: "",
        disabled: false
      },
      data: [],
      departmentList: [],
      departmentProps: {
        value: "departmentId",
        label: "departmentName",
        children: "children"
      }
    };
  },
  created() {
    this.getDepartment();
    this.getData();
  },
  methods: {
    /**
     * @description 搜索数据
     * @function searchData
     * @return {type} {description}
     */
    searchData() {
      this.listQuery.page = 1;
      this.getData();
    },
    /**
     * @description 删除部门子部门
     * @function {function name}
     * @return {type} {description}
     */
    deleteChildren(children) {
      for (let iterator of children) {
        if (iterator.children.length > 0) {
          this.deleteChildren(iterator.children);
        } else {
          delete iterator.children;
        }
      }
    },
    /**
     * @description 获取部门列表
     * @function {function name}
     * @return {type} {description}
     */
    getDepartment() {
      Permission.getDepartment().then(res => {
        this.deleteChildren([res.data]);
        this.departmentList[0] = res.data;
      });
    },
    /**
     * @description 获取数据
     * @function getData
     * @return {type} {description}
     */
    getData() {
      Permission.getUser(this.listQuery).then(res => {
        this.data = res.data;
        this.total=res.total;
      });
    },
    /**
     * @description 数据添加
     * @function addData
     * @return {type} {description}
     */
    addData(
      obj = {
        disabled: false,
        roles: []
      }
    ) {
      this.editData = {
        roles: obj.roles,
        userId: obj.userId,
        username: obj.username,
        password: obj.password,
        departmentName: obj.departmentName,
        name: obj.name,
        departmentId: obj.departmentId,
        office: obj.office,
        disabled: obj.disabled
      };
      this.dialogFormVisible = true;
    },
    /**
     * @description 获取部门名称
     * @function getDepartmentName
     * @return {type} {description}
     */
    getDepartmentName(idList) {
      let id = idList[idList.length - 1],
        nameObj = this.departmentList,
        resultName = "";
      const getname = arr => {
        for (let item of arr) {
          if (item.departmentId == id) {
            resultName = item.departmentName;
          } else {
            if (item.children) {
              getname(item.children);
            }
          }
        }
      };
      getname(nameObj);
      return resultName;
    },
    /**
     * @description 提交数据
     * @function {function name}
     * @return {type} {description}
     */
    submitData(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.editData.departmentId.length === 0) {
            this.$message.warning("请选择部门");
            return false;
          }
          this.editData.departmentName = this.getDepartmentName(
            this.editData.departmentId
          );
          if (!this.editData.userId) {
            Permission.createUser(this.editData).then(res => {
              this.$message.success("添加成功");
              this.getData();
              this.cancelEdit();
            });
          } else {
            Permission.editUser(this.editData).then(res => {
              this.$message.success("编辑成功");
              this.getData();
              this.cancelEdit();
            });
          }
        } else {
          return false;
        }
      });
    },
    /**
     * @description 数据重置
     * @function cancelEdit
     * @return {type} {description}
     */
    cancelEdit(formName = "dataForm") {
      this.$refs[formName].resetFields();
      this.editData = {
        roles: [],
        userId: "",
        username: "",
        password: "",
        name: "",
        departmentId: [],
        departmentName: "",
        office: "",
        disabled: false
      };
      this.dialogFormVisible = false;
    },
    /**
     * @description 删除数据
     * @function deleteData
     * @return {type} {description}
     */
    deleteData(userId) {
      this.$confirm("确定删除该用户?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Permission.deleteUser({
            userId
          }).then(res => {
            this.$message({
              type: "success",
              message: "成功删除"
            });
            this.getData();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    disabledUser(userId, disabled) {
      Permission.disabledUser({
        userId,
        disabled: !disabled
      }).then(res => {
        this.$message({
          type: "success",
          message: "操作成功"
        });
        this.getData();
      });
    }
  }
};
</script>
